export default {
  state: {
    arr: [{ value: 0, name: "" }, { value: 1, name: "" }, { value: 2, name: "" }, { value: 3, name: "" }, { value: 4, name: "" }],
    get(s) {
      return s === 0 ? "" : s === 1 ? "" : s === 2 ? "" : s === 3 ? "" : s === 4 ? "" : "--";
    }
  },
  noticeType: [{ value: "SYS", name: "" }, { value: "OP", name: "" }]
}

